// const imageOptions = {
//   scale: 5,
//   encoderOptions: 1,
//   backgroundColor: 'white',
// }
var urlbackend = require("../../env/env")();

function TemplatePrint(props) {
  const [show, setShow] = useState(false);
  const [provider, setprovider] = useState(props.providerinfo);

  useEffect(() => {


  
    setprovider(props.providerinfo?.data[0])
 
 

  }, [])

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);


  return (
    <>
      <Button variant="success"
        onClick={handleShow}> <span className="fa fa-print"></span></Button>


      <Modal size="xl" show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title> Fast Box  </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div style={{ paddingLeft: 50 }} >
            <PrintPage Template={props.Template} providerinfo={provider}></PrintPage>
          </div>
        </Modal.Body>
        <Modal.Footer>
          {/*  <Button onClick={printemplate}>Télécharger <FaCloudDownloadAlt /></Button>  */}
          <Button variant="secondary" onClick={handleClose}>
            Fermer
          </Button>

        </Modal.Footer>
      </Modal>
    </>
  );
}

export default TemplatePrint