
import React, { useEffect, useState } from 'react';
import {
    Row,
    Col,
    Card,
    Button,
    Modal,
    FormControl,
    InputGroup
} from "react-bootstrap";
import { Formik } from "formik";
import * as Yup from "yup";
import CustomInput from "../../Components/CustomInput";
import InputStatus from "../../Components/InputStatus"

import Aux from "../../hoc/_Aux";
import axios from 'axios';
var urlbackend = require("../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp, "Phone number is not valid")
        .required("Phone is Required"),
    city: Yup.string()
        .min(2, "City is Too Short!")
        .max(20, "City is Too Long!")
        .required("City is Required"),
    description: Yup.string()
        .min(2, "description is Too Short!")
        .max(20, "description is Too Long!")
        .required("description is Required"),
    name: Yup.string()
        .min(2, "name is Too Short!")
        .max(20, "name is Too Long!")
        .required("name is Required"),
    code: Yup.string()
        .min(2, "code is Too Short!")
        .max(20, "code is Too Long!")
        .required("code is Required"),
});
function TicketAdd(props) {
    const [show, setShow] = useState(false);
    //const [pickupsUpdate] = useState(props.pickup)
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    useEffect(() => {

    }, [])
    const handlSubmit = (values) => {

        console.log(props.colis.id)
        console.log(values)
        console.log(props.colis.id)

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.post(urlbackend + "/ticket/add", {
            "message": values.message,
            "colis": props.colis.id,
            "type": values.type,
            "priority": values.priority,
            "value": {
                price: values.price,
                city: values.city,
                gouvernorat: values.gouvernorat,
                address: values.address,
                type_envoi: values.type_envoi,
                payment_mode: values.payment_mode,

            }
        }, config)
            .then(response => {
                alert("add Succefull")
                console.log(response.data)
                handleClose()
            })
            .catch(e => {
                console.log(e)
            })
    };
    const showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">Ajouter  demande pour modifier colis</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Row>
                                    <Col md={12}>
                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="type"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.type}
                                            >
                                                <option>choisir Type de demande</option>

                                                {["Change Prix", "Change date livraison", "changer addresse","changer N. de téléphone", "changer mode de payement", "changer type d'envoi"].map((driver, index) => (
                                                    <option key={driver} value={driver}>
                                                        {driver}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>

                                        <label htmlFor="basic-url">Priorité</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="priority"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}

                                            >
                                                <option>choose priorité</option>

                                                {[{ name: "Haute", value: "2" }, { name: "moyenne", value: "1" }, { name: "failbe", value: "0" }].map((agence, index) => (
                                                    <option key={agence.name} value={agence.value}>
                                                        {agence.name}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>
                                        <CustomInput
                                            name="message"
                                            label="message"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.message}
                                            errors={errors.message}
                                            touched={touched.message}
                                            type="message"
                                        />
                                        {values.type === "Change Prix" && <CustomInput
                                            name="price"
                                            label="prix"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.price}
                                            errors={errors.price}
                                            touched={touched.price}
                                        />}
                                        {values.type === "changer addresse" && <CustomInput
                                            name="city"
                                            label="city"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.city}
                                            errors={errors.city}
                                            touched={touched.city}
                                            type="message"
                                        />}
                                        {values.type === "changer addresse" && <CustomInput
                                            name="gouvernorat"
                                            label="gouvernorat"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.gouvernorat}
                                            errors={errors.gouvernorat}
                                            touched={touched.gouvernorat}
                                        />}
                                        {values.type === "changer addresse" && <CustomInput
                                            name="address"
                                            label="address"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.address}
                                            errors={errors.address}
                                            touched={touched.address}
                                        />}
                                            {values.type === "changer N. de téléphone" && <CustomInput
                                            name="phone_number"
                                            label="N. de téléphone"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.phone_number}
                                            errors={errors.phone_number}
                                            touched={touched.phone_number}
                                        />}
                                        {values.type === "changer type d'envoi" &&   
                                        <div>
                                         <label htmlFor="basic-url">type_envoi <InputStatus errors={errors.type_envoi} touched={touched.type_envoi} /></label>

                                         <InputGroup className="mb-3">
                                             <FormControl
                                                 as="select"
                                                 id="type_envoi"
                                                 custom
                                                 onChange={handleChange}
                                                 onBlur={handleBlur}
                                                 value={values.type_envoi}
                                             >
                                                 <option>type envoi</option>
                                         
                                                 {["LIVRAISON A DOMICILE", "ECHANGE", "RECUPERATION"].map((type_envoi, index) => (
                                                     <option key={type_envoi} value={type_envoi}>
                                                         {type_envoi}
                                                     </option>
                                                 ))}
                                             </FormControl>
                                         </InputGroup>
                                         </div>}
                                    </Col>
                                    <Col md={6}>








                                    </Col>
                                </Row>

                                <Button
                                    onClick={() => {
                                        handlSubmit(values);
                                        handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    Envoyer demande
                                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };
    return (
        <>
            <Button variant="secondary"
                onClick={handleShow}> <span className="fa fa-ticket"></span></Button>


            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title> </Modal.Title>
                </Modal.Header>
                <Modal.Body>


                    <Formik
                        initialValues={{
                            message: "",
                            price: props.colis.price,
                            city: props.colis.city,
                            address: props.colis.address,
                            gouvernorat: props.colis.gouvernorat,
                            type_envoi: props.colis.type_envoi,
                            payment_mode: props.colis.payment_mode,
                            phone_number: props.colis.phone_number

                        }}
                        onSubmit={(values, { setSubmitting }) => {
                            this.submitForm(values);
                            setSubmitting(false);
                        }}
                        validationSchema={SignupSchema}
                    >
                        {(props) => showForm(props)}
                    </Formik>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Fermer
                    </Button>
                    {/* <Button variant="primary" onClick={handleClose}>
            Save Changes
            </Button> */}
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default TicketAdd